$(document).ready(function(){
  $("#jquery_jplayer_1").jPlayer({
    ready: function () {
      $(this).jPlayer("setMedia", {
        mp3: "Eternal_Structures.mp3",
        oga: "Eternal_Structures.ogg"
      });
    },
    loadeddata: function(event){ // calls after setting the song duration

            var minutes = Math.floor( event.jPlayer.status.duration / 60 );
            var seconds = Math.floor( event.jPlayer.status.duration - minutes * 60 );
            var fullPodcastDuration = minutes + ':' + seconds;

            $('.jp-full-time').text( fullPodcastDuration );

    },
    swfPath: "/js",
    supplied: "mp3,oga"
  });
});